import React, { useState } from "react";
import Card from "./Card";
import styled from "styled-components";
import ActionButton from "../ActionButtons";
import { brandColor } from "../../../../globals/colors";
const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 1rem;
  .header {
    font-size: 1.8rem;
    color: ${brandColor};
    margin: 0.3rem 1rem;
  }
  p {
    margin: 0.5rem 1rem;
  }
`;
const CardsSection = styled.div`
  flex-grow: 1;
  display: flex;
  flex-wrap: wrap;
`;
const Options = ({ items, next, skipable }) => {
  const [selected, setSelected] = useState();
  console.log("here", { items });
  return (
    <Wrapper>
      <h2 className="header">Each Industry does things diffrently</h2>
      <p>
        In hac habitasse platea dictumst. Aenean gravida mauris lorem, in
        dapibus leo aliquet quis. Nullam et maximus odio. Phasellus velit
        lectus, fermentum ac turpis quis, rutrum cursus urna. Nullam sed auctor
        nisi, non pretium magna. Nunc tristique metus sapien, a aliquam arcu
        maximus et. Curabitur tortor arcu, tincidunt eget felis sit amet,
        facilisis mattis tellus.{" "}
      </p>
      <CardsSection>
        {items.map((item, index) => (
          <Card
            key={index}
            {...item}
            selected={item.id === selected}
            onSelect={() =>
              setSelected(item.id === selected ? undefined : item.id)
            }
          />
        ))}
      </CardsSection>
      <ActionButton>
        <button disabled={selected === undefined} onClick={next}>
          Continue
        </button>
        {skipable && (
          <button className="reverse" onClick={next}>
            Skip Step
          </button>
        )}
      </ActionButton>
    </Wrapper>
  );
};
export default Options;
