import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { motion, useAnimation } from "framer-motion";
import { blueAccent, whiteColor } from "../../../../globals/colors";
import sleepAsync from "../../../../utilities/sleepAsync";
import ActionButton from "../ActionButtons";
const Wrapper = styled.div`
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: ${({ flip }) => (flip ? "row-reverse" : "row")};
  .content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    height: 100%;

    padding: 0.5rem;
    button {
      color: ${whiteColor};
      background-color: ${blueAccent};
      padding: 1rem 4rem;
      margin-top: 1rem;
      border: 0;
      font-size: 1.1rem;
      cursor: pointer;
    }
    body {
      flex-grow: 1;
    }
  }
  h1 {
    color: ${blueAccent};
    font-size: 2.3rem;
    font-weight: 500;
  }
  p {
  }
`;
const Illustration = styled.div`
  min-width: 40%;
  height: 100%;
  .box {
    height: 100%;
    background: ${({ bg }) => bg};
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      max-width: 90%;
    }
  }
`;
const Question = ({ next, media, flip, skipable }) => {
  const initialPosition = flip ? "200%" : "-100%";
  const illustrationControls = useAnimation();
  const contentControls = useAnimation();
  const runShowAnimation = async () => {
    await illustrationControls.start({
      left: "0%",
      transition: { duration: 0.3 },
    });
    await contentControls.start({
      opacity: 1,
      transition: { duration: 0.3, type: "spring" },
    });
  };
  const runHideAnimation = async () => {
    Promise.all([
      contentControls.start({
        opacity: 0,
        transition: { duration: 0.2, type: "spring" },
      }),
      illustrationControls.start({
        left: initialPosition,
        transition: { duration: 0.2 },
      }),
    ]);
    await sleepAsync(400);
    if (next) next();
  };
  useEffect(() => {
    runShowAnimation();
    return () => console.log("removing");
  }, []);
  return (
    <Wrapper flip={flip}>
      <Illustration bg={media.bg}>
        <motion.div
          className={"box"}
          initial={{ left: initialPosition, position: "relative" }}
          animate={illustrationControls}
        >
          <img src={media.path} />
        </motion.div>
      </Illustration>
      <motion.div
        initial={{ opacity: 0 }}
        animate={contentControls}
        className={"content"}
      >
        <div className={"body"}>
          <h1>The value of Customer onboarding</h1>
          <p>
            Pellentesque malesuada lacus vitae nisl ultricies, eu sodales urna
            ornare. Aenean vel mauris vel est egestas volutpat. Sed tristique
            diam in neque vestibulum vestibulum. Maecenas non nulla quis augue
            laoreet consectetur. Phasellus cursus vestibulum augue at semper.
          </p>
        </div>
        <ActionButton>
          <button disabled={undefined} onClick={runHideAnimation}>
            Continue
          </button>
          {skipable && (
            <button className="reverse" onClick={runHideAnimation}>
              Skip Step
            </button>
          )}
        </ActionButton>
      </motion.div>
    </Wrapper>
  );
};
export default Question;
