import React, { useState } from "react";
import CssBaseline from "@material-ui/core/CssBaseline";
import Container from "./Themes/default/Container";
import LandingPage from "./apps/LandingPage";
import Survey from "./apps/Survey";
const pages = [LandingPage, Survey];
const App = () => {
  const [activeIndex, setActiveIndex] = useState(0);
  const next = () => {
    if (pages.length - 1 > activeIndex) setActiveIndex(activeIndex + 1);
  };
  const getActivePages = () => {
    const Page = pages[activeIndex];
    return <Page next={next} />;
  };

  return <Container baseStyle={<CssBaseline />}>{getActivePages()}</Container>;
};

export default App;
