import React, { useEffect } from "react";
import styled from "styled-components";
import { brandColor, whiteColor } from "../../globals/colors";
import logo from "../../assets/view@2x.png";
import landingPageIllustration from "../../assets/illustrations/landing-page-1.png";
import { motion, useAnimation } from "framer-motion";
const SplashScreenWrapper = styled.div`
  flex-grow: 1;
  .box {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    color: ${whiteColor};
    background: ${brandColor};
    height: 100%;
    padding: 3rem;
    .header {
      width: 100%;
      height: 5%;

      img {
        width: auto;
        max-height: 100%;
      }
    }
    .illustration {
      text-align: center;
      height: 70%;
      img {
        max-height: 100%;
      }
    }
    .content {
      display: flex;
      flex-direction: column;
      width: 100%;
      align-items: flex-start;
      height: 25%;
    }
    h1 {
      margin: 1rem 0 0 0;
      padding: 0;
      font-size: 2.5rem;
    }
    p {
      max-width: 35rem;
      font-size: 1.4rem;
    }
  }
`;

const variants = {
  visible: { left: 0 },
  hidden: { left: "-100%" },
};
const SplashScreen = ({ sidebarVisible, onMouseMove, hide, onHidden }) => {
  const controls = useAnimation();
  useEffect(() => {
    if (!hide) return;
    runHideAnimation();
  }, [hide]);
  const runHideAnimation = async () => {
    await controls.start({
      left: "-100%",
      transition: { duration: 0.6 },
    });
    onHidden();
  };
  return (
    <SplashScreenWrapper
      isFullWidth={!sidebarVisible}
      onMouseMove={onMouseMove}
    >
      <motion.div className={"box"} initial={{ left: 0 }} animate={controls}>
        <div className={"header"}>
          <img src={logo} />
        </div>
        <div className={"illustration"}>
          <img src={landingPageIllustration} />
        </div>
        <div className={"content"}>
          <h1>The State of Customer Onboarding</h1>
          <p>
            An interactive survey of industry best practices and how they can
            change the way you do business.
          </p>
        </div>
      </motion.div>
    </SplashScreenWrapper>
  );
};

export default SplashScreen;
