import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { motion, useAnimation } from "framer-motion";
import { whiteColor } from "../globals/colors";
import sleepAsync from "../utilities/sleepAsync";
const Wrapper = styled.div`
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  .paper-box {
    overflow: hidden;
    width: 70%;
    max-width: 70rem;
    height: 80%;
    padding: 0;
    background: ${whiteColor};
    box-shadow: 1px 1px 2px 1px #00000021;
  }
`;
const SurveyBox = ({
  animate,
  onAnimationComplete,
  sleep,
  children,
  actions,
}) => {
  const controls = useAnimation();
  const [showActions, setShowActions] = useState(false);
  const runHideAnimation = async () => {
    if (sleep) await sleepAsync(sleep);
    await controls.start({
      width: "70%",
      transition: { duration: 0.5 },
    });
    setShowActions(true);
    if (onAnimationComplete) onAnimationComplete();
  };
  useEffect(() => {
    if (!animate) return;
    runHideAnimation();
  }, [animate]);

  return (
    <Wrapper>
      <motion.div
        className={"paper-box"}
        initial={{
          ...(animate && { width: 0 }),
        }}
        animate={controls}
      >
        {children}
      </motion.div>

      {showActions && <div>{actions}</div>}
    </Wrapper>
  );
};
export default SurveyBox;
