import React, { useState } from "react";
import GlobalHeader from "../../components/GlobalHeader";
import styled from "styled-components";
import SurveyBox from "../../components/SurveyBox";
import LoadingBar from "../../components/LoadingBar";
import TimeEstimate from "../../components/TimeEstimate";
import { blueAccent } from "../../globals/colors";
import screens from "./data.json";
import screenTypes from "./secreenTypes";
const Wrapper = styled.div`
  height: 100%;
  display: flex;
  flex-direction: column;
`;
const Survey = () => {
  const [showContentBox, setShowContentBox] = useState(false);
  const [showContent, setShowContent] = useState(false);
  const [screenIndex, setScreenIndex] = useState(0);
  const getScreen = () => {
    const screenDetail = screens[screenIndex];
    //todo error handeling
    const Screen = screenTypes[screenDetail.screenType];
    const isLastScreen = screens.length === screenIndex + 1;
    return (
      <Screen
        key={screenIndex}
        {...screenDetail}
        next={() => !isLastScreen && setScreenIndex(screenIndex + 1)}
      />
    );
  };
  return (
    <Wrapper>
      <GlobalHeader
        animate
        sleep={300}
        onAnimationComplete={() => setShowContentBox(true)}
        right={<TimeEstimate min={10} />}
      >
        <LoadingBar progress={5} />
      </GlobalHeader>
      {showContentBox && (
        <SurveyBox
          animate
          onAnimationComplete={() => setShowContent(true)}
          actions={
            <div
              style={{
                color: blueAccent,
                fontWeight: 600,
                margin: "1rem",
                cursor: "pointer",
              }}
            >
              Save for later
            </div>
          }
        >
          {showContent && getScreen()}
        </SurveyBox>
      )}
    </Wrapper>
  );
};
export default Survey;
