import React, { useEffect } from "react";
import styled from "styled-components";
import { motion, useAnimation } from "framer-motion";
import { blueAccent, whiteColor } from "../../globals/colors";
const ActionPromptWrapper = styled.div`
  .box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    h2,
    p,
    button,
    div {
      max-width: 25rem;
      padding: 0;
      margin: 0;
    }
    div {
      width: 100%;
    }
    h2 {
      font-size: 1.8rem;
      font-weight: 600;
      margin-bottom: 0.5rem;
    }
    p {
      font-weight: 400;
    }
    button {
      color: ${whiteColor};
      background-color: ${blueAccent};
      padding: 1rem 4rem;
      margin-top: 1rem;
      border: 0;
      font-size: 1.1rem;
      cursor: pointer;
    }
  }
`;
const variants = {
  hidden: { width: 0 },
  visible: { width: "35%" },
};
const ActionPrompt = ({ onClick }) => {
  const controls = useAnimation();
  useEffect(() => {
    controls.start({
      width: "35rem",
      transition: { duration: 0.45 },
    });
  }, []);
  const handleClick = async () => {
    controls.start({
      opacity: 0,
      transition: { type: "spring", duration: 0.8 },
    });
    onClick();
  };
  return (
    <ActionPromptWrapper>
      <motion.div
        className={"box"}
        initial={{ opacity: 1, width: 0 }}
        animate={controls}
      >
        <h2>Compare your Customer onboarding to leaders in your industry</h2>
        <p>
          Get near real-time analytics on where your companys customer
          onboarding stands compared to your industry using real data. Use the
          results to identify key actions to take and track your outcomes as you
          go by updating the state of your onboarding as you go.
        </p>
        <div>
          <button onClick={handleClick}>Get Started</button>
        </div>
      </motion.div>
    </ActionPromptWrapper>
  );
};

export default ActionPrompt;
